.navbar-container{
    height: 56px;
    background-color: var(--flipkart-lightBlue);
    color: white;
    position: fixed;
    width: 100%;
    z-index: 2;
}
.navbar{
    max-width: 1248px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    padding: 10px 124px;
}
.navbar-logo{
    width: 80px;
}
.navbar-search{
    display: flex;
    justify-content: space-between;
    max-width: 444px;
    width: calc(100%-540px);
    background: #fff;
    border-radius: 2px;
}

.searchbtn{
    height: 36px;
    width: 44px;
    border: none;
    background-color: white;
    font-size: 20px;
    padding: 4px 12px 0 8px;
    color: var(--flipkart-lightBlue);
    cursor: pointer;
}

.navbar-searchbox{
    padding: 10px;
    width: 100%;
    border: none;
    outline: none;
}

.navbar-btn{
    padding: 5px 40px;
    height: 32px;
    background-color: #fff;
    color: var(--flipkart-lightBlue);
    border: 1px solid #dbdbdb;
    font-weight: 700;
    cursor: pointer;
}

.navbar-bcs{
    font-size: 13px;
    cursor: pointer;
}

.navbar-more{
    font-size: 13px;
    position: relative;
}

.moredown{
    position: absolute;
    top: 10%;
    padding: 0 20px;
}

.navbar-cart{
    display: flex;
    align-items: center;
    margin-left: 20px;
    cursor: pointer;
}

.cart{
    font-size: 15px;
    padding: 0 5px;
    color: #fff;
}

